html,
body,
p,
ol,
ul,
li,
dl,
dt,
dd,
blockquote,
figure,
fieldset,
legend,
textarea,
pre,
iframe,
hr,
h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
        Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 16px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-size: 100%;
    font-weight: normal;
}

ul {
    list-style: none;
}

button,
input,
select,
textarea {
    margin: 0;
}

html {
    box-sizing: border-box;
}

* {
    box-sizing: inherit;
    &:before,
    &:after {
        box-sizing: inherit;
    }
}

img,
embed,
iframe,
object,
audio,
video {
    height: auto;
    max-width: 100%;
}

iframe {
    border: 0;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
}

td,
th {
    padding: 0;
    text-align: left;
}

%console {
    overflow-x: auto;
    white-space: pre-wrap;
    background: #000;
    color: #fff;
    font-family: Consolas, 'Andale Mono WT', 'Andale Mono', 'Lucida Console',
        'Lucida Sans Typewriter', 'DejaVu Sans Mono', 'Bitstream Vera Sans Mono',
        'Liberation Mono', 'Nimbus Mono L', Monaco, 'Courier New', Courier,
        monospace;
}

pre {
    @extend %console;
}

* {
    box-sizing: border-box;
}

#console {
    @extend %console;
    width: 314px;
    height: 153px;
    position: fixed;
    top: 20px;
    right: 20px;
    opacity: 0.75;
    max-width: 100%;

    ul,
    ol,
    li {
        list-style: none;
    }

    li {
        margin: 10px;

        &:before {
            content: '$: ';
        }
    }

    z-index: 5;
}

body {
    background-color: #434343;

    html.min-width & {
        min-width: 1110px;
    }

    html.custom-scrollbar & {
        &::-webkit-scrollbar {
            width: 8px;
        }
        &::-webkit-scrollbar-track {
            background: black;
        }
        &::-webkit-scrollbar-thumb {
            background: grey;
        }
    }
}

#sticky {
    position: sticky;
    top: 0;
    width: 120px;
    background: red;
    left: 0;
    height: 60px;
}

#nav {
    position: fixed;
    top: 0;
    right: 0;
    z-index: 6;
    text-align: right;

    button {
        appearance: none;
        background: white;
        margin: 2px;
        border: 0 none;
        outline: none;
        color: black;
        border-radius: 0;
    }
}

#wrapper {
    display: flex;
    flex-direction: column;

    html.horizontal & {
        display: block;
        white-space: nowrap;

        > div {
            width: 420px;
            display: inline-block;
            white-space: normal;
            vertical-align: top;
        }
    }
}
